.contacts{
  &__groups{
    margin-top: 2.5em;
    @include grid100gap(1.75em);
  }
}

.contacts-group{
  &__title{
    text-align: center;
    text-transform: uppercase;
    color: $primary;
  }
  &__inner{
    border-radius: 1em;
    padding: 1.625em;
    margin-top: 1.5em;
    background-color: #1b1b1b;
    display: flex;
    justify-content: center;
  }
  .contact-item{
    margin: 0 1.25em;
  }
  &__items{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
}

.contact-item{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon{
    @include center;
    width: 1.25em;
    margin-right: .5em;
    img,svg{
      width: 100%;
    }
    path{
      fill: #fff;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 120%;
    text-transform: uppercase;
    font-weight: 600;
  }
  &__bottom{
    height: 2.5em;
    display: flex;
    align-items: center;
    border-radius: 1em;
    font-weight: 300;
    font-family: $font2;
    background-color: #5b431e;
    margin-top: .5em;
    padding: 0 .75em;
  }
  &__link{
    font-size: 1.125em;
    line-height: 120%;
  }
  &__copy{
    margin-left: 1.25em;
    width: 1em;
    @include center;
    svg,
    img{
      width: 100%;
    }
    ._view2{
      display: none;
    }
  }
}

.contact-card{
  &__items{
    display: flex;
    justify-content: space-between;
  }
}


@media screen and (max-width: 1100px){
  .contacts-group{
    &__items{
      grid-template-columns: repeat(2,calc(50% - 1.125em));
      gap: 2.25em;
    }
  }

  .contact-card{
    &__items{
      @include grid100gap(1.125em);
    }
    .contact-item{
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__groups{
      margin-top: 1.5em;
    }
  }

  .contacts-group{
    &__title{
      font-size: 1.375em;
    }
    &__inner{
      padding: 1.625em 2.125em;
      margin-top: 1em;
      background-color: #1b1b1b;
      @include grid100gap(1.125em);
    }
    .contact-item{
      margin: 0;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .contact-item{
    display: block;
    &__icon{
      width: 1em;
    }
    &__title{
      font-size: 1em;
    }
    &__bottom{
      height: 2.25em;
      justify-content: space-between;
    }
    &__link{
      font-size: 1em;
    }
  }


}
