.page-block{
  padding: 1.5em 0 2.5em;
  &__top{
    @include grid100gap(.625em);
  }
  &__title{
    text-transform: uppercase;
  }
}

.vacancies{
  .tabs{
    margin-top: 2em;
  }
  &__items{
    margin-top: 1.875em;
    @include grid100gap(1.875em);
  }
}

.vacancy-item{
  padding: 2.125em 2.5em;
  border: 2px solid #3c3c3c;
  border-radius: 1em;
  background-image: url(../img/vacancy-bg.png);
  background-size: 28em auto;
  background-position: left 0 top -5em;
  background-repeat: no-repeat;
  transition: all .3s;
  &:not(.vacancy-detail):hover{
    border-color: $primary;
    background-color: #211b13;
    color: $primaryLight;
    .vacancy-item__tag{
      background-color: #3a3023;
      color: #79664e;
    }
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    color: $primaryLight;
  }
  &__desc{
    margin-top: 2.375em;
    max-width: 41.25em;
  }
  &__bottom{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: .25em;
  }
  &__tags{
    display: flex;
  }
  &__tag{
    transition: all .3s;
    display: flex;
    align-items: center;
    height: 1.375em;
    background-color: #353535;
    padding: 0 .75em;
    border-radius: .25em;
    color: #787878;
    margin-right: .75em;
    span{
      font-size: .875em;
    }
  }
  &__link{
    width: 12em;
  }
}


@media screen and (max-width: 1100px){

}


@media screen and (max-width: 576px){
  .page-block{
    &__top{
      text-align: center;
      &._hide-mob{
        display: none;
      }
    }
  }

  .vacancies{
    &__items{
      margin-top: 1.625em;
      gap: 1.625em;
    }
  }

  .vacancy-item{
    padding: 1em 1em 6.75em;
    display: flex;
    flex-direction: column;
    position: relative;
    &__top{
      @include grid100gap(.75em);
    }
    &__desc{
      margin-top: 1.75em;
    }
    &__bottom{
      order: -1;
      margin: 0 0 .75em;
    }
    &__link{
      position: absolute;
      bottom: 2.25em;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
