.vacancy-detail{
  margin-top: 1.5em;
  background: #1b1b1b;
  .vacancy-item__bottom{
    margin-bottom: 2em;
    order: -1;
  }
  .vacancy-item__tag{
    background-color: #3a3023;
    color: #79664e;
  }
  .vacancy-item__price{
    color: $primaryLight;
  }
  @media screen and (min-width: 577px){
    display: flex;
    flex-direction: column;
    .vacancy-item__title{
      font-size: 1.875em;
    }
    .vacancy-item__price{
      margin-top: -1.2em;
    }
  }
  .text{
    max-width: 41.5em;
    margin-top: 1.75em;
  }
}

.hr{
  margin-top: 2.25em;
  &__title{
    text-align: center;
    text-transform: uppercase;
    color: $primaryLight;
  }
  &__grid{
    width: 60.25em;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .875em));
    gap: 1.75em;
    margin: 2em auto 0;
  }
}

.hr-card{
  padding: 2em 3em;
  border-radius: 1em;
  background-color: #1b1b1b;
  display: flex;
  align-items: flex-end;
  &__btn{
    margin-left: 1.25em;
    flex-shrink: 0;
    height: 2.5em;
    padding: 0 1.5em;
    img{
      width: 1.1875em;
      margin-right: .75em;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 1100px){

}


@media screen and (max-width: 576px){
  .vacancy-detail{
    padding-left: .75em;
    padding-right: .75em;
    padding-bottom: 2.5em;
    .vacancy-item__bottom{
      margin-bottom: 1.5em;
    }
  }

  .hr{
    margin-top: 2.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .hr-card{
    padding: 1.75em 2.5em;
    flex-direction: column;
    align-items: flex-start;
    &__btn{
      margin: .875em 0 0;
    }
    .contact-item{
      width: 100%;
    }
  }
}
