[data-theme="light"]{
  .m-page{
    color: $black;
    background: #dcdcdc;
  }
  .main-slide{
    background-image: unset;
  }
  .vacancy-card{
    background-image: url(../img/magnifier2-white.png);
    background-color: #d4d4d4;
    border: 2px solid #aaaaaa;
    &__link{
      background-color: #5b431e;
    }
  }
  .primary-text{
    color: #5b431e;
  }
  .e-shop-card{
    background-color: #d4d4d4;
    border-color: #aaaaaa;
    &._active{
      border-color: #e3ae5d;
      background-color: #5b431e;
    }
    &:not(._active){
      .e-shop-card__title{
        color: $black;
      }
    }
  }
  .footer__logo{
    opacity: 1;
  }
  .footer__c{
    color: #b6b6b6;
  }
  .tabs{
    background-color: #3d3d3d;
  }
  .tab{
    color: $white;
    &._active{
      color: $black;
    }
  }
  .vacancy-item{
    background-color: #dadada;
    border-color: #aaaaaa;
    &:not(.vacancy-detail):hover{
      background-color: #563f1c;
      .vacancy-item__price,
      .vacancy-item__title{
        color: #d9b56f;
      }
      .vacancy-item__tag{
        background-color: #c29754;
        color: #ffd799;
      }
    }
    &__price,
    &__title{
      color: #5b431e;
    }
    &__tag{
      background-color: #7b7b7b;
      color: #cfcfcf;
    }
  }
  .breadcrumbs{
    color: $white;
  }
  .qa-card{
    &__show{
      background-color: #bdbdbd;
    }
    &__hidden{
      background: #dadada;
      border: 1px solid #aaaaaa;
    }
  }
  .tool-card{
    background-color: #5b431d;
    color: $white;
  }
  .contacts-group{
    &__title{
      color: $primaryDark;
    }
    &__inner{
      border: 1px solid #aaaaaa;
      background-color: transparent;
    }
  }
  .contact-item{
    &__icon{
      path{
        fill: #826d52;
      }
    }
    &__bottom{
      background-color: #c3c3c3;
    }
    &__copy{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  .hr__title{
    color: $primaryDark;
  }
  .hr-card{
    border: 1px solid #aaaaaa;
    background-color: transparent;
    &__btn{
      background-color: #826d52;
      color: $white;
      &:hover{
        background-color: $primaryDark;
        color: $white;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
