.catalog-cards{
  margin: 3.125em auto 0;
  max-width: 1345px;
  width: calc(100% - 7em);
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2em));
  gap: 2em 3em;
  &._hide-desktop{
    @media screen and (min-width: 1101px){
      display: none;
    }
  }
}

.catalog-card{
  border: 1px solid #cdab68;
  border-radius: 1em;
  &__top{
    padding: 1.75em .5em;
    @include center;
  }
  &__icon{
    width: 2.8125em;
    height: 2.8125em;
    border-radius: .625em;
    background-color: #251a08;
    margin-right: .75em;
    flex-shrink: 0;
    @include center;
    img{
      width: 1.3125em;
    }
    ._view2{
      display: none;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 1.2;
    font-weight: 600;
    text-transform: uppercase;
  }
  &__cart{
    height: 1.25em;
    width: 1.25em;
    @include center;
    display: inline-flex;
    background-image: url(../img/cart.png);
    @include bgImgSize(100%);
    img{
      width: 100%;
    }
  }
  &__table{
    table{
      width: 100%;
      border-spacing: 0;
    }
    th{
      color: #fcbc59;
      &._bg{
        background-color: #251a08;
      }
    }
    th,td{
      font-weight: 600;
      text-align: center;
      height: 2.375em;
      border-top: 1px solid #cdab68;
      border-right: 1px solid #cdab68;
      &:last-child{
        border-right: none;
      }
      &:nth-child(3){
        width: 3.5em;
      }
      span{
        font-size: 1.5em;
      }
    }
  }
}

.catalog-table{
  margin: 3.125em auto 0;
  max-width: 1560px;
  width: calc(100% - 7em);
  border: 1px solid #f0b254;
  &__images{
    padding: 1em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - .5em));
    gap: .75em;
  }
  &__img{
    @include imgCover(39.1%);
  }
  table{
    width: 100%;
    border-spacing: 0;
  }
  th{
    color: #fcbc59;
  }
  th,td{
    font-weight: 600;
    text-align: center;
    height: 2em;
    border-top: 1px solid #cdab68;
    border-right: 1px solid #cdab68;
    padding: 0 .125em;
    &:nth-child(even){
      background-color: #251a08;
    }
    &:last-child{
      border-right: none;
    }
    span{
      font-size: 1.125em;
    }
    small{
      font-size: .8125em;
    }
  }
}


@media screen and (max-width: 1100px){
  .catalog-cards{
    grid-template-columns: repeat(2,calc(50% - 1.5em));
  }

  .catalog-table{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .catalog-cards{
    margin-top: 1.5em;
    grid-template-columns: 100%;
    gap: 1.5em;
    width: calc(100% - 2.5em);
  }

  .catalog-card{
    font-size: .75em;
  }
}
