.footer{
  padding: 2.5em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    opacity: .1;
    width: 14.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c{
    margin-top: 1em;
    color: #1f1f1f;
  }
}

.footer-nav{
  display: flex;
  &__link{
    font-size: 1.125em;
    line-height: 1.2;
    letter-spacing: -0.025em;
    margin-right: 1.55em;
    &:last-child{
      margin-right: 0;
    }
  }
}


@media screen and (max-width: 1100px){
  .footer{
    padding: 1.25em 0;
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-bottom: 5.625em;
    &__col2{
      display: none;
    }
    &__c{
      margin-top: 0;
    }
  }

  .footer-nav{
    display: none;
  }
}
