[data-theme="light"]{
  .filter-item{
    background-color: #3d3d3d;
    color: $white;
    &._desktop-view{
      color: $black;
      .filter-item {
        &__show {
          background-color: #d4d4d4;
        }
        &__hidden{
          background-color: #d4d4d4;
          border-color: #c3c3c3;
        }
      }
    }
  }
  .catalog-filter{
    &__btn{
      color: $primaryDark;
      path{
        fill: $primaryDark;
      }
    }
  }
  .catalog{
    border-color: #826d52;
  }
  .catalog-card{
    border-color: #3d3d3d;
    td,th{
      border-color: #3d3d3d;
    }
    th{
      color: #7c7060;
      &._bg{
        background-color: #aaaaaa;
      }
    }
    &__icon{
      background-color: #826d52;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__cart{
      background-image: url(../img/cart2.png);
    }
  }
  .catalog-table{
    border-color: #808080;
    color: #404040;
    th{
      color: #404040;
    }
    th,td{
      border-color: #808080;
      &:nth-child(even){
        background-color: #826d52;
        color: $white;
      }
    }
  }
  .header-btn{
    &--tg-chat{
      color: $white;
    }
    path{
      fill: #826d52;
    }
    &__copy{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  .desktop-menu{
    color: $white;
  }
  .theme-btn{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
  .lang{
    &__show{
      border-color: #826d52;
      path{
        fill: #826d52;
      }
    }
    &__hidden{
      border-color: #acacac;
      background-color: #f0f0f0;
    }
    &__item{
      &:hover,
      &._active{
        color: #7d6643;
      }
    }
  }
  .header__logo{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
  .header-action{
    &__link{
      path{
        fill: #826d52;
      }
    }
  }
  .burger{
    path{
      fill: #826d52;
    }
  }
  .mob-menu{
    background: #c8c8c8;
    &__link{
      color: #3d3d3d;
      path{
        fill: #3d3d3d;
      }
      &._active{
        color: #826d52;
        path{
          fill: #826d52;
        }
      }
    }
  }
  .modal__dialog{
    border-color: #c3c3c3;
    background-color: #d4d4d4;
  }
  .close-btn{
    path{
      fill: #826d52;
    }
  }
  @media screen and (max-width: 576px){
    .filter-item{
      background-color: transparent;
      &._mob-view{
        color: $black;
        .filter-item {
          &__show {
            background-color: #d4d4d4;
          }
          &__hidden{
            background-color: #d4d4d4;
            border-color: #c3c3c3;
          }
        }
      }
      &._type2{
        .filter-option{
          label{
            color: $black !important;
          }
        }
      }
    }
  }
}
