.mob-menu__bg,
.mob-menu,
.mob-panel{
  display: none;
}


@media screen and (max-width: 1100px){
  .mob-menu{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 5;
    background-image: linear-gradient(90.0deg, #1d1d1d -25%, #101010 50%, #1d1d1d 125%);
    width: 100%;
    height: $vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 4em;
    &__link{
      display: flex;
      align-items: center;
      padding: 0 2.5em;
      font-weight: 500;
      text-transform: uppercase;
      border-top: 1px solid #282828;
      height: 3em;
      span{
        font-size: 1em;
      }
      svg{
        width: 1.375em;
        margin-right: .625em;
      }
      path{
        fill: $white;
      }
      &._active{
        path{
          fill: $primary;
        }
      }
    }
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      z-index: 5;
    }
    @media screen and (min-width: 577px){
      width: 28.5em;
      justify-content: unset;
      padding-top: 7.75em;
      &__link{
        padding: 0 3.75em;
        &:last-child{
          border-bottom: 1px solid #282828;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .mob-panel{
    z-index: 6;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #1b1b1b;
    height: 3.875em;
    padding: 0 1.25em;
    @include center;
    &__btn{
      width: 1.5em;
      height: 1.5em;
      @include center;
      margin: 0 1em;
      svg{
        max-width: 100%;
        max-height: 100%;
      }
      &._active{
        path{
          fill: $primary;
        }
      }
    }
  }


}
