.trade{
  .contacts-group__items{
    margin-top: .5em;
  }
}

.catalog{
  padding: 2em 0;
  margin: 5em auto 0;
  border: 3px solid #5b431e;
  border-radius: 1.5em;
  max-width: 1595px;
  width: 100%;
  &__title{
    text-align: center;
    margin-bottom: 2em;
  }
}

.filter-item{
  margin-top: 1.25em;
  background-color: #1b1b1b;
  border-radius: 1em;
  padding: .375em 1em;
  &__show{
    display: none;
  }
  &__items{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: -.375em;
  }
  .filter-option{
    margin-top: .375em;
  }
  &._desktop-view{
    background-color: transparent !important;
    border-radius: 0;
    position: relative;
    padding: 0;
    .filter-item{
      &__show{
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 1px solid #3c3c3c;
        height: 2.375em;
        padding: 0 1em;
        background-color: #1b1b1b;
        border-radius: 1em;
        position: relative;
        z-index: 3;
        &:after{
          content: '';
          flex-shrink: 0;
          width: 1em;
          height: 1em;
          background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' width='512' height='512' x='0' y='0' viewBox='0 0 492.004 492.004' style='enable-background:new 0 0 512 512' xml:space='preserve' class=''%3E%3Cg transform='matrix(6.123233995736766e-17,1,-1,6.123233995736766e-17,492.00401306152344,-0.00004577636721592171)'%3E%3Cpath d='M382.678 226.804 163.73 7.86C158.666 2.792 151.906 0 144.698 0s-13.968 2.792-19.032 7.86l-16.124 16.12c-10.492 10.504-10.492 27.576 0 38.064L293.398 245.9l-184.06 184.06c-5.064 5.068-7.86 11.824-7.86 19.028 0 7.212 2.796 13.968 7.86 19.04l16.124 16.116c5.068 5.068 11.824 7.86 19.032 7.86s13.968-2.792 19.032-7.86L382.678 265c5.076-5.084 7.864-11.872 7.848-19.088.016-7.244-2.772-14.028-7.848-19.108z' fill='%23616161' opacity='1' data-original='%23000000' class=''%3E%3C/path%3E%3C/g%3E%3C/svg%3E");
          @include bgImgSize(100%);
          margin-left: .5em;
          transition: all .3s;
        }
      }
      &__current{
        font-size: .875em;
      }
      &__hidden{
        position: absolute;
        z-index: 2;
        top: 1.125em;
        left: 0;
        width: 100%;
        padding: 2em .75em .75em;
        border-radius: 0 0 1em 1em;
        background-color: #1b1b1b;
        transition: all .3s;
        opacity: 0;
        pointer-events: none;
      }
      &__items{
        display: block;
      }
    }
    .filter-option{
      margin-top: 0;
      label{
        padding: .375em 0;
        height: auto;
        border-radius: 0;
        justify-content: unset;
        font-weight: 400;
        font-family: $font1;
      }
      span{
        font-size: .875em;
        line-height: 110%;
      }
      input:checked{
        &~label{
          color: #cdab68;
          background-color: transparent;
        }
      }
    }
    &._open{
      z-index: 5;
      .filter-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
      .filter-item__show:after{
        transform: rotate(-180deg);
      }
    }
  }
}

.filter-option{
  label{
    padding: 0 1.5em;
    height: 2.125em;
    border-radius: .625em;
    @include center;
    font-weight: 500;
    font-family: $font2;
  }
  span{
    font-size: 1.125em;
    letter-spacing: -0.025em;
  }
  ._view2{
    display: none;
  }
  input:checked{
    &~label{
      color: $black;
      background-color: #ffbe5a;
    }
  }
}

.city{
  margin-top: 1.25em;
  @include center;
  &__info{
    display: flex;
    align-items: center;
    font-family: $font2;
    font-weight: 500;
    margin-right: 1.625em;
    img{
      width: 1.625em;
      margin-right: .75em;
    }
  }
  .filter-item{
    margin: 0;
    width: 22em;
    max-width: 100%;
    @media screen and (min-width: 577px){
      &__show{
        border-radius: .75em !important;
      }
      &__hidden{
        border-radius: 0 0 .75em .75em !important;
      }
      &__show,
      &__hidden{
        font-size: 1.25em;
      }
    }
  }
}

.catalog-filter{
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.25em;
  }
  &__hint{
    font-size: 1.125em;
    line-height: 120%;
    text-transform: uppercase;
    font-weight: 600;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__btn{
    font-weight: 500;
    font-family: $font2;
    color: $primary;
    display: flex;
    align-items: center;
    margin-left: 2.25em;
    &:hover{
      opacity: .8;
    }
    svg,
    img{
      width: 1.375em;
      margin-right: 1em;
    }
    span{
      font-size: 1.125em;
      line-height: 1.2;
      text-decoration: underline;
    }
    &--big{
      img{
        width: 1.625em;
      }
    }
  }
}


@media screen and (max-width: 1100px){
  .catalog{
    padding: 0 0 2em;
    margin-top: 8.75em;
    border: none;
    border-radius: 0;
    max-width: 57.625em;
    &__title{
      margin-bottom: 1.5em;
    }
  }

  .catalog-filter{
    &__wrap{
      position: relative;
    }
    &__bottom{
      justify-content: center;
    }
    &__nav{
      height: 3em;
      position: absolute;
      top: 0;
      right: 0;
    }
    &__btn{
      margin-left: 2em;
      img{
        width: 1.1875em;
        margin-right: .875em;
      }
      span{
        font-size: 1em;
      }
      &--big{
        img{
          width: 1.375em;
        }
      }
    }
  }

  .city{
    justify-content: unset;
    &__info{
      margin-right: 1em;
      span{
        display: none;
      }
      img{
        margin: 0;
        width: 2.5em;
      }
    }
    .filter-item{
      width: 18.75em;
    }
  }
}


