          justify-content: space-between;
          border: 1px solid #3c3c3c;
          height: 2.375em;
          padding: 0 1em;
          background-color: #1b1b1b;
          border-radius: 1em;
          position: relative;
          z-index: 3;
          &:after{
            content: '';
            flex-shrink: 0;
            width: 1em;
            height: 1em;
            background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' width='512' height='512' x='0' y='0' viewBox='0 0 492.004 492.004' style='enable-background:new 0 0 512 512' xml:space='preserve' class=''%3E%3Cg transform='matrix(6.123233995736766e-17,1,-1,6.123233995736766e-17,492.00401306152344,-0.00004577636721592171)'%3E%3Cpath d='M382.678 226.804 163.73 7.86C158.666 2.792 151.906 0 144.698 0s-13.968 2.792-19.032 7.86l-16.124 16.12c-10.492 10.504-10.492 27.576 0 38.064L293.398 245.9l-184.06 184.06c-5.064 5.068-7.86 11.824-7.86 19.028 0 7.212 2.796 13.968 7.86 19.04l16.124 16.116c5.068 5.068 11.824 7.86 19.032 7.86s13.968-2.792 19.032-7.86L382.678 265c5.076-5.084 7.864-11.872 7.848-19.088.016-7.244-2.772-14.028-7.848-19.108z' fill='%23616161' opacity='1' data-original='%23000000' class=''%3E%3C/path%3E%3C/g%3E%3C/svg%3E");
            @include bgImgSize(100%);
            margin-left: .5em;
            transition: all .3s;
          }
        }
        &__current{
          font-size: .875em;
        }
        &__hidden{
          position: absolute;
          z-index: 2;
          top: 1.125em;
          left: 0;
          width: 100%;
          padding: 2em .75em .75em;
          border-radius: 0 0 1em 1em;
          background-color: #1b1b1b;
          transition: all .3s;
          opacity: 0;
          pointer-events: none;
        }
        &__items{
          display: block;
        }
      }
      .filter-option{
        margin-top: 0;
        label{
          padding: .375em 0;
          height: auto;
          border-radius: 0;
          justify-content: unset;
          font-weight: 400;
          font-family: $font1;
        }
        span{
          font-size: .875em;
          line-height: 110%;
        }
        input:checked{
          &~label{
            color: #cdab68;
            background-color: transparent;
          }
        }
      }
      &._open{
        z-index: 5;
        .filter-item__hidden{
          opacity: 1;
          pointer-events: unset;
        }
        .filter-item__show:after{
          transform: rotate(-180deg);
        }
      }
    }
    &._type2{
      .filter-item__items{
        flex-wrap: nowrap;
        justify-content: center;
        margin: 0;
      }
      .filter-option{
        margin: 0 .875em;
        label{
          justify-content: unset;
          padding: 0;
          height: unset;
          background-color: transparent !important;
          color: $white !important;
          font-weight: 400;
          font-family: $font1;
          &:before{
            content: '';
            width: 1.5em;
            height: 1.5em;
            flex-shrink: 0;
            border: 1px solid #3c3c3c;
            border-radius: 50%;
            margin-right: .5em;
            background-image: url(../img/radio.svg);
            @include bgImgSize(0);
          }
        }
        span{
          font-size: .875em;
        }
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
        input:checked~label:before{
          background-size: .8125em auto;
        }
      }
    }
  }

  .catalog-filter{
    &__wrap{
      position: relative;
    }
    &__hint{
      display: none;
    }
    &__nav{
      height: unset;
      position: static;
      flex-direction: column;
    }
    &__btn{
      margin: 0 0 .625em;
      &:last-child{
        margin: 0;
      }
      img{
        width: 1.25em;
        margin-right: .625em;
      }
      span{
        font-size: .875em;
      }
    }
  }

  .city{
    &__info{
      display: none;
    }
    .filter-item{
      width: 100%;
    }
  }
}
