@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $white;
    background-image: linear-gradient(90.0deg, #1d1d1d -25%, #101010 50%, #1d1d1d 125%);
    font-size: calc(1224px * (16 / 1224));
    @media screen and (max-width: 1224px){
        font-size: calc(100vw * (16 / 1224));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.container {
    max-width: 1224px;
    width: 100%;
    margin: 0 auto;
    padding: 0 3.5em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 1100px){
    .m-page{
        font-size: calc(100vw * (16 / 1080));
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 320));
    }

    .container{
        padding: 0 1.25em;
    }
}


