.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .9;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background-color: #0e0e0e;
        width: 54.5em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2.75em 3em;
        border: 1px solid #ffbe5a;
        border-radius: 1em;
        display: inline-block;
    }
    &__top{
        text-align: center;
    }
    .close-btn{
        position: absolute;
        top: 1em;
        right: 1em;
        width: .875em;
        @include center;
        svg,
        img{
            width: 100%;
        }
    }
    &__contacts{
        display: flex;
        justify-content: space-between;
        margin-top: 1.25em;
    }
    &__hint{
        text-align: center;
        margin-top: 2.5em;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: 2.25em 1.5em;
        }
        &__contacts{
            @include grid100gap(1em);
        }
        &__hint{
            text-align: left;
            margin-top: 1.5em;
        }
    }
}
