.tools{
  &__answers{
    @include grid100gap(1.125em);
    margin-top: 1.75em;
  }
  &__items{
    margin-top: 5.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
  }
}

.qa-card{
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .5em 2.125em;
    min-height: 2.875em;
    border-radius: 1em;
    background-color: #232323;
    position: relative;
    z-index: 2;
    cursor: pointer;
  }
  &__title{
    font-size: 1.125em;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
  }
  &__btn{
    width: 1.25em;
    height: 1.25em;
    flex-shrink: 0;
    background-image: url(../img/arrow_bottom.png);
    @include bgImgSize(100%);
  }
  &__hidden{
    background-image: linear-gradient(0.0deg, #282828 -10%, #151515 111%);
    border-radius: 0 0 1em 1em;
    padding: 3.5em 2em 2.25em;
    margin-top: -1.75em;
    display: none;
  }
  &._open{
    .qa-card__btn{
      transform: rotate(-180deg);
    }
  }
}

.tool-card{
  background-color: #2e2f2e;
  padding: 2.125em;
  border-radius: 1em;
  display: flex;
  flex-direction: column;
  &__title{
    color: $primaryLight;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 133%;
    margin-top: 1.25em;
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 1.75em;
  }
  &__link{
    width: 12em;
  }
}


@media screen and (max-width: 1100px){
  .tools{
    &__items{
      margin-top: 4.5em;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .tools{
    &__answers{
      gap: 1em;
      margin-top: 1.5em;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .qa-card{
    &__show{
      padding-right: 1.25em;
    }
    &:not(.contact-card){
      .qa-card{
        &__show{
          padding: 1em;
        }
        &__title{
          font-size: .75em;
          line-height: 150%;
        }
        &__btn{
          margin-left: 2em;
          width: 3.25em;
          height: 3.25em;
          background-image: url(../img/plus.png);
        }
        &__hidden{
          padding: 3.125em 1em 2em;
          margin-top: -2em;
        }
      }
      &._open{
        .qa-card__btn{
          transform: rotate(-45deg);
        }
      }
    }
  }

  .tool-card{
    padding: 1.5em 2.25em;
    &__desc{
      font-size: .875em;
      line-height: 150%;
    }
    &__nav{
      padding-top: 2.5em;
    }
  }
}
