.tabs{
  display: flex;
  justify-content: center;
  padding: .375em;
  background-color: #1b1b1b;
  border-radius: 1em;
  min-height: 2.875em;
  font-family: $font2;
}

.tab{
  height: 2.125em;
  padding: 0 1.375em;
  display: flex;
  align-items: center;
  position: relative;
  border-radius: .625em;
  transition: all .3s;
  font-weight: 500;
  span{
    font-size: 1.125em;
    line-height: 120%;
  }
  &:hover{
    cursor: pointer;
    color: $primary;
  }
  &._active{
    color: $black;
    background: $primary;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}


@media screen and (max-width: 1100px){

}


@media screen and (max-width: 576px){
  .tabs{
    overflow-x: auto;
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    padding-left: 1.25em;
    padding-right: 1.25em;
    justify-content: unset;
    border-radius: 0;
  }

  .tab{
    flex-shrink: 0;
    font-size: .875em;
  }
}
