@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    height: 2.75em;
    border-radius: 1em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $black;
    &:hover{
        background-color: darken($primary,10%);
        color: $black;
    }
}
