.e-shops{
  padding: 2.5em 0;
  &__title{
    text-align: center;
  }
  &__grid{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.e-shop-card{
  background-color: #1B1B1B;
  border: 1px solid #3C3C3C;
  border-radius: 1.5em;
  padding: 1.25em 2em;
  &._active{
    background-color: #211B13;
    border-color: #FFBE5A;
    color: $primaryLight;
  }
  &__title{
    color: $primaryLight;
  }
  &__desc{
    margin-top: 1.75em;
  }
}

.main-vacancies{
  padding: 1em 0;
  overflow: hidden;
}

.vacancies-slider{
  @media screen and (min-width: 1101px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 3.5em / 3));
        gap: 1.75em;
        transform: unset !important;
      }
    }
  }
}

.vacancy-card{
  height: 100%;
  padding: 1.25em 1.875em;
  background-color: #1A1A1A;
  background-image: url(../img/magnifier2.png);
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: right top;
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  &__desc{
    margin-top: .5em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.125em;
    display: flex;
  }
  &__link{
    height: 2.125em;
    padding: 0 1.5em;
    background-color: #373737;
    color: $white;
    border-radius: 1em;
    span{
      font-size: 1.125em;
    }
  }
}

.main{
  padding: 1.625em 0 .625em;
}

.swiper-button{
  width: 2.25em;
  img{
    width: 100%;
    display: block;
  }
  &:hover{
    opacity: .8;
  }
}

.main-slider{
  position: relative;
  border: 2px solid #5b431e;
  border-radius: 1.25em;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      &-prev{
        left: 4.375em;
      }
      &-next{
        right: 4.375em;
      }
    }
    &-pagination{
      display: none;
    }
  }
}

.main-slide{
  padding: 1.5em 12.5em;
  display: grid;
  grid-template-columns: calc(100% - 13.75em) 13.5em;
  gap: 1.25em;
  align-items: center;
  background-image: url(../img/main-map.png);
  background-size: 25.75em auto;
  background-repeat: no-repeat;
  background-position: left 12.5em center;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: 1em;
  }
}

.swiper-pagination{
  @include center;
  min-height: 1em;
  &-bullet{
    background-color: #1b1b1b;
    opacity: 1;
    margin: 0 .375em !important;
    width: .75em;
    height: .75em;
    &-active{
      background-color: $primaryLight;
      width: 1em;
      height: 1em;
    }
  }
}


@media screen and (max-width: 1100px){
  .e-shops {
    padding: 2.25em 0;
    &__grid{
      margin: 3.75em auto 0;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
      width: 46.125em;
      max-width: 100%;
    }
  }

  .vacancies-slider{
    .swiper{
      &-slide{
        width: 23.75em;
        padding-right: 1.75em;
      }
    }
  }

  .main{
    padding-top: .625em;
  }

  .main-slider{
    .swiper{
      &-button{
        &-prev{
          left: 2em;
        }
        &-next{
          right: 2em;
        }
      }
    }
  }

  .main-slide{
    padding-right: 8em;
    padding-left: 8em;
  }
}


@media screen and (max-width: 576px){
  .e-shops{
    padding: 1.25em 0 .625em;
    &__title{
      padding: 0 .75em;
      text-align: left;
      br{
        display: none;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
      margin-top: 3em;
    }
  }

  .e-shop-card{
    padding: 1em 1.25em;
    &__desc{
      margin-top: 1.25em;
    }
  }

  .vacancies-slider{
    .swiper{
      &-container{
        width: calc(100% + .75em);
      }
      &-slide{
        width: 95%;
        padding-right: .75em;
      }
    }
  }

  .vacancy-card{
    padding: 1.5em;
    &__desc{
      margin-top: .75em;
    }
  }

  .main-slider{
    border: none;
    border-radius: 0;
    .swiper{
      &-button{
        display: none;
      }
      &-pagination{
        display: flex;
        position: static;
        margin-top: 1.875em;
      }
    }
  }

  .main-slide{
    padding: 0;
    grid-template-columns: 100%;
    gap: 2em;
    background-image: unset;
    &__img{
      order: -1;
    }
    &__title{
      font-size: 1.125em;
      max-width: 11.55em;
    }
    &__desc{
      font-size: .9375em;
      line-height: 133%;
      margin-top: 1.67em;
    }
  }
}
