.header{
  position: relative;
  z-index: 10;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 14.5em;
    margin-right: auto;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  .container{
    max-width: 1848px;
  }

  @media screen and (max-width: 1700px){
    @media screen and (min-width: 1101px){
      font-size: calc(100vw * (16 / 1700));
    }
  }
  &._open{
    .mob-menu{
      opacity: 1;
      pointer-events: unset;
      &__bg{
        display: block;
      }
    }
    .mob-panel__btn{
      path{
        fill: $white;
      }
    }
    .mob-panel__btn._toggle-menu{
      path{
        fill: $primary;
      }
    }
  }
}

.burger{
  display: none;
}

.header-top{
  padding: .75em 0;
  border-bottom: 1px solid #2d2314;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__hint{
    color: #4f4f4f;
  }
  &__nav{
    display: flex;
  }
}

.header-btn{
  display: flex;
  align-items: center;
  border: 1px solid #433115;
  border-radius: .9375em;
  height: 2.375em;
  padding: 0 .75em;
  margin-left: 1.5em;
  &--tg-chat{
    background-color: #433115;
  }
  svg,
  &__icon{
    width: 1.125em;
    margin-right: .5em;
  }
  &__title{
    font-size: 1em;
  }
  &__copy{
    margin-left: .5em;
    ._view2{
      display: none;
    }
  }
}

.copy-btn{
  width: 1em;
  flex-shrink: 0;
  @include center;
  img{
    width: 100%;
  }
}

.header-bottom{
  padding-top: 1.25em;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__link{
    display: flex;
    align-items: center;
    font-weight: 600;
    text-transform: uppercase;
    margin-left: 1.25em;
    svg{
      margin-right: .3125em;
      width: 1.125em;
    }
    span{
      font-size: .9375em;
    }
  }
}

.lang{
  position: relative;
  &._open{
    .lang__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    width: 4em;
    height: 2.125em;
    border: 2px solid $white;
    border-radius: 1em;
    @include center;
    font-weight: 600;
    text-transform: uppercase;
    cursor: pointer;
    svg{
      width: 1.125em;
      margin-right: .25em;
    }
    span{
      font-size: .6875em;
    }
  }
  &__hidden{
    position: absolute;
    top: 100%;
    right: 0;
    margin-top: .5em;
    width: 16.25em;
    padding: .875em 1.25em;
    border: 1px solid #2d2d2d;
    border-radius: .625em;
    background-color: #181818;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__hint{
    font-size: .9375em;
    line-height: 150%;
    font-weight: 500;
  }
  &__items{
    column-count: 2;
    column-gap: .25em;
    margin-top: .5em;
  }
  &__item{
    display: inline-flex;
    align-items: center;
    width: 100%;
    margin-bottom: .375em;
    img{
      width: .8125em;
      margin-right: .25em;
    }
    span{
      font-size: .9375em;
      line-height: 140%;
    }
    &._active{
      color: #f6b148;
    }
  }
}

.theme-btn{
  margin-right: 1.5em;
  width: 2em;
  flex-shrink: 0;
  @include center;
  img{
    width: 100%;
    display: block;
  }
  ._view2{
    display: none;
  }
}

.desktop-menu{
  flex-grow: 1;
  max-width: 69.375em;
  margin-right: 1.5em;
  margin-left: 1.5em;
  background-color: #5b431e;
  border-radius: 1.25em;
  display: flex;
  align-items: center;
  &__home{
    border-radius: 1.25em 0 0 1.25em;
    height: 4.625em;
    width: 4.625em;
    background-color: #433115;
    @include center;
    svg{
      width: 1.375em;
    }
    path{
      fill: #fff;
    }
    &._active{
      path{
        fill: #ffbe5a;
      }
    }
  }
  &__items{
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1em;
  }
  &__item{
    font-weight: 900;
    font-size: 1.125em;
    text-transform: uppercase;
    margin: 0 1.25em;
    &._active{
      color: $primary;
    }
  }
}


@media screen and (max-width: 1100px){
  .header-top{
    display: none;
  }

  .header-bottom{
    padding: 1.5em 0;
    position: relative;
    z-index: 6;
    &__inner{
      justify-content: unset;
    }
  }

  .header-action{
    margin-left: auto;
  }

  .burger{
    width: 2.5em;
    @include center;
    margin-right: 2em;
    flex-shrink: 0;
    svg{
      width: 100%;
    }
  }

  .theme-btn{
    margin-right: 1.375em;
  }

  .desktop-menu{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      width: 9.25em;
    }
  }

  .header-bottom{
    padding: 1.25em 0;
  }

  .header-action{
    &__link{
      display: none;
    }
  }

  .burger{
    display: none;
  }

  .theme-btn{
    margin-right: .5em;
  }
}
