@import "../vars.scss";
.breadcrumbs {
    display: flex;
    margin-bottom: 1.625em;
    ul{
        display: flex;
        background-color: #3b2b13;
        border-radius: .3125em;
    }
    li{
        &:first-child{
            border-top-left-radius: .3125em;
            border-bottom-left-radius: .3125em;
        }
        &:not(:last-child){
            margin-right: .75em;
            position: relative;
            background-color: #5b431e;
            &:before{
                content: '';
                position: absolute;
                top: 0;
                left: 100%;
                width: .9375em;
                height: 100%;
                background-image: url(../img/triangle.svg);
                @include bgImgSize(100%);
                margin-left: -1px;
            }
            &:first-child+li{
                margin-left: -.75em;
                a{
                    padding-left: 1.5em;
                }
            }
        }
        &:last-child{
            pointer-events: none;
        }
    }
    a{
        padding: 0 1em;
        font-size: .875em;
        height: 1.86em;
        display: flex;
        align-items: center;
    }
}


